import React from "react";
import {Swiper, SwiperSlide} from 'swiper/react';
import 'swiper/components/navigation/navigation.min.css';
import 'swiper/components/effect-flip/effect-flip.min.css';
import {useTranslation} from 'react-i18next';


// import Swiper core and required modules
import SwiperCore, {
    EffectFlip, Pagination, Navigation
} from 'swiper';
import {graphql, useStaticQuery} from "gatsby";

// install Swiper modules
SwiperCore.use([EffectFlip, Pagination, Navigation]);


export default function Feedbacks() {
    const {i18n} = useTranslation();
    const {t} = useTranslation();
    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
        filter: {categories: {nodes: {elemMatch: {name: {eq: "testimonials"}}}}}

      ) {
          nodes {
            id
            title
            content
            date(formatString: "YYYY/MM/DD")
            tags {
              nodes {
                name
              }
            }
            language {
              slug
            }
          }
      }
    }
  `)
    return (
        <div className="pt-14 relative">
            <div className="container mx-auto px-4">
                <div className="text-2xl md:text-5xl text-center glitch" data-glitch={t('testimonials')}>
                    {t('testimonials')}
                </div>

                <div className="container relative mx-auto pt-14">
                    <div className="grid grid-cols-1 lg:grid-cols-6">
                        <div className="lg:col-start-2 lg:col-span-4">
                            <Swiper navigation={true} effect={'flip'} grabCursor={true}
                                    slidesPerView={1}
                                    className="mySwiper">
                                {data.allWpPost.nodes.map((node, index) => (
                                    node.language.slug === i18n.language &&
                                    <SwiperSlide key={index} className="swiper-slide flex justify-center items-center">
                                    <blockquote className="blockquote">
                                    <p className="lead-big color-white mb-0"><em dangerouslySetInnerHTML={{__html: node.content}}/></p>
                                    <h6 className="mt-5">- {node.title}</h6>
                                    </blockquote>
                                    </SwiperSlide>
                                    ))}
                            </Swiper>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    )
}
